$gridParts: 12;
$gridSize: 100% / $gridParts;
$breakpoints: (
  sm: 768px,
  md: 992px,
  lg: 1200px
);

.img-r { width: 100%; }
.row:after {
  clear: both;
  content: " ";
  display: table;
}

.col-xs-1, .col-sm-1, .col-md-1, .col-lg-1, .col-xs-2, .col-sm-2, .col-md-2, .col-lg-2, .col-xs-3, .col-sm-3, .col-md-3, .col-lg-3, .col-xs-4, .col-sm-4, .col-md-4, .col-lg-4, .col-xs-5, .col-sm-5, .col-md-5, .col-lg-5, .col-xs-6, .col-sm-6, .col-md-6, .col-lg-6, .col-xs-7, .col-sm-7, .col-md-7, .col-lg-7, .col-xs-8, .col-sm-8, .col-md-8, .col-lg-8, .col-xs-9, .col-sm-9, .col-md-9, .col-lg-9, .col-xs-10, .col-sm-10, .col-md-10, .col-lg-10, .col-xs-11, .col-sm-11, .col-md-11, .col-lg-11, .col-xs-12, .col-sm-12, .col-md-12, .col-lg-12 {
  position: relative;
  min-height: 1px;
  padding-right: -25px;
  padding-left: -25px;
}

@for $i from 0 through $gridParts {
  .col-xs-#{$i}        { width: $i * $gridSize; float: left; }
  .col-xs-pull-#{$i}   { right: $i * $gridSize; }
  .col-xs-push-#{$i}   { left: $i * $gridSize; }
  .col-xs-offset-#{$i} { margin-left: $i * $gridSize; }
}

@each $name, $size in $breakpoints {
  @media (min-width: $size) {
    @for $i from 0 through $gridParts {
      .col-#{$name}-#{$i}        { width: $i * $gridSize; float: left; }
      .col-#{$name}-pull-#{$i}   { right: $i * $gridSize; }
      .col-#{$name}-push-#{$i}   { left: $i * $gridSize; }
      .col-#{$name}-offset-#{$i} { margin-left: $i * $gridSize; }
    }
  }
}
